.App {
  width: 90%;
  margin: 5% auto;
  max-width: 909px;
}
.infoDiv {
  display: flex;
}
.infoHead1 {
  color: #ffa03d;
  font-size: 12px;
  font-weight: bold;
}
.infoText1 {
  color: rgba(34, 34, 34, 0.5);
  font-size: 12px;
  margin-left: 5px;
}
.Lock-img {
  margin-right: 3px;
}
.ContentDiv{
  display: flex;
}
.MainDiv {
  background-color: #ffffff;
  border-radius: 8px;
  padding: 10px 0 0;
}
.titleStyle {
  color: #222222;
  font-size: 16px;
}
.service-logo {
  height: 16px;
  padding: 0 10px;
  margin-bottom: -2px;
}
.contentKeyText {
  color: #222222;
  opacity: 0.5;
  font-size: 14px;
}
.contentValText {
  color: #222222;
  font-size: 14px;
}
.contentInfoView {
  display: inline-flex;
  width: 96%;
  padding: 2%;
}
.contentInfoView2{
  display: inline-flex;
  width: calc(46% - 1px);
  padding: 2%;
}
.contentInfoView2BorderRight{
  border-right: solid rgba(34, 34, 34, 0.05) 1px;
}
.titleDiv{
  padding: 1%;
  border-bottom: solid rgba(34, 34, 34, 0.05) 1px;
}
.requestDiv{
  width: 60%;
}
.requestDivContent{
  border: 1px solid rgba(34, 34, 34, 0.05);
  box-sizing: border-box;
  box-shadow: 0px 2px 4px rgba(34, 34, 34, 0.05);
  border-radius: 8px;
}
.requestDivMargin{
  margin: 3% 3% 1%;
}
.memberDiv{
  width: 40%;
  border-left: solid rgba(34, 34, 34, 0.05) 1px;
}
.memberView{
  display: inline-flex;
  width: 100%;
  padding-top: 3%;
}
.memberCountView{
  width: 10%;
  margin: auto;
}
.memberCount{
  height: 20px;
  width: 20px;
  background-color: #F2F2F2;
  border-radius: 10px;
  text-align: center;
  margin: auto;
}
.memberCountText{
  opacity: 0.5;
  color: #222222;
  font-size: 12px;
  margin: 0px;
  padding-top: 2px;
}
.memberDetails{
  display: inline-flex;
  width: 80%;
}
.memberCheck{
  width: 10%;
  margin: auto;
}
.contentKeyDiv{
  width: 30%;
}
.contentKeyDiv2{
  width: 110%;
}
.contentValDiv, .contentValDiv3{
  width: 69%;
  padding-left: 1%;
}
.contentValDiv3{
  margin-top: 3px;
}
.topBorder{
  border-top: solid rgba(34, 34, 34, 0.05) 1px;
}
.alertText{
  font-size: 12px;
  color: #FF6448;
  padding: 1% 3%;
}
.defaultColor{
  color: #222222;
  opacity: 0.5;
}
.memberDivTitle{
  opacity: 0.5;
  color: #222222;
  font-size: 12px;
  margin: 0;
}
.member_avatar{
  height: 25px;
  width: 25px;
}
.memberAvatarHolder{
  margin: auto 5px;
}
.memberSection{
  padding: 3%;
}
.memberName{
  color: #222222;
  font-size: 14px;
}
.memberInfo{
  color: #222222;
  opacity: 0.5;
  font-size: 12px;
}
.request_avatar{
  height: 20px;
  width: 20px;
  margin-bottom: -4px;
  margin-right: 5px;
}
.fileIcon{
  height: 12px;
  width: auto;
  margin-right: 4px;
  margin-bottom: 0;
}
.membersHr{
  border-color: rgba(34, 34, 34, 0.05);
  border-width: 0.5px;
  margin: 0 5%;
}
.mobileActionButtons{
  display: none;
}
.webActionButtons{
  display: block;
  padding: 1% 3% 2%;
}
.buttonGroup{
  display: inline-flex;
  width: 210px;
}
.actionButton{
  width: 50%;
  text-align: center;
  margin: 2%;
  padding: 2%;
  border-radius: 10px;
  cursor: pointer;
}
.approveButton{
  background-color: #26C281;
}
.approveButton p{
  color: #FFFFFF;
  margin: 0;
}
.denyButton{
  background-color: #FF6448;
}
.denyButton p{
  color: #FFFFFF;
  margin: 0;
}
.memberDetailP{
  display: -webkit-box;
  overflow : hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical; 
  margin: 0;
}

@media only screen and (max-width: 425px) {
  .App {
    width: 100%;
    margin: 5% auto;
  }
  .App-logo{
    padding: 4%;
  }
  .infoDiv {
    display: block;
    margin-top: 5px;
    border-top: solid rgba(34, 34, 34, 0.05) 1px;
    padding: 0 3% 3%;
  }
  .infoHead1 {
    margin-bottom: 0px;
  }
  .infoText1 {
    margin-top: 5px;
  }
  .titleDiv{
    border-bottom: none !important;
  }
  .MainDiv{
    border-radius: 0 !important;
  }
  .ContentDiv{
    display: block;
  }
  .requestDiv{
    width: 100%;
    border-bottom: solid #F2F2F2 4px;
  }
  .memberDiv{
    width: 100%;
    border-left: none;
    box-shadow: 0px -1px 0px rgba(34, 34, 34, 0.05), 0px 1px 0px rgba(34, 34, 34, 0.05);
  }
  .requestDivMargin{
    margin: 4% 4% 1%;
  }
  .alertText{
    padding: 4%;
  }
  .contentInfoView{
    width: 94%;
    padding: 3%;
  }
  .contentInfoView2{
    width: 94%;
    padding: 3%;
  }
  .contentInfoView2BorderRight{
    border-right: none;
  }
  .contentInfoView3{
    display: block;
  }
  .contentKeyDiv, .contentKeyDiv2{
    width: 40%;
  }
  .contentValDiv{
    width: 60%;
  }
  .contentValDiv3{
    width: 100%;
  }
  .memberSection{
    padding: 4%;
  }
  .mobileActionButtons{
    display: block;
    padding: 4% 1% 1%;
  }
  .webActionButtons{
    display: none;
  }
  .buttonGroup{
    width: 100%;
  }
}
